import React, { Fragment } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Link } from "gatsby"
import { publicationInfoList } from "../contents/publication-info"
import PublicationItem from "../components/publication-item"
import PublicationSelection from "../components/publication-selection"

//<Link to="/publications/">more</Link>

const Publications = () => (
  <Layout>
    <SEO
      title="Publications"
      description="Publish in Human-Computer Interaction, Health, and Medical venues."
      keywords={[
        `Pei-Yao Hung`,
        `Human-Computer Interaction`,
        `user experience`,
        `researcher`,
        `publications`,
        `CSCW`,
        `PervasiveHealth`,
        `CHI`,
        `MobileHCI`,
        `ECSCW`,
        `IDC`,
        `C&T`,
      ]}
    />
    <div className="content">
      <h1 className="title is-3 is-spaced is-size-4-mobile">Publications</h1>
      <div className="content has-text-justified">
        <div className="subtitle is-4 is-spaced is-size-5-mobile">
          Refereed Conference
        </div>
        <PublicationSelection projectFilterFunction={(pub) => {
            return pub.category == "conference"
          }}></PublicationSelection>
        <hr />
        <div className="subtitle is-4 is-spaced is-size-5-mobile">
          Journal Article
        </div>
        <PublicationSelection projectFilterFunction={(pub) => {
            return pub.category == "journal"
          }}></PublicationSelection>
        <hr />
        <div className="subtitle is-4 is-spaced is-size-5-mobile">
          Book Chapter
        </div>
        <PublicationSelection projectFilterFunction={(pub) => {
            return pub.category == "book_chapter"
          }}></PublicationSelection>
        <hr />
        <div className="subtitle is-4 is-spaced is-size-5-mobile">
          Refereed Workshop &#038; Doctoral Consortium
        </div>
        <PublicationSelection projectFilterFunction={(pub) => {
            return pub.category == "workshop"
          }}></PublicationSelection>
        <hr />

        <div className="subtitle is-4 is-spaced is-size-5-mobile">
          Work-In-Progress
        </div>
        <PublicationSelection projectFilterFunction={(pub) => {
            return pub.category == "work_in_progress"
          }}></PublicationSelection>
        <hr />
        {publicationInfoList
          .filter(pub => {
            return pub.category == "in_submission"
          }).length > 0 ? <div className="subtitle is-4 is-spaced is-size-5-mobile">
          In Submission
        </div> : null
        }
        <PublicationSelection projectFilterFunction={(pub) => {
            return pub.category == "in_submission"
          }}></PublicationSelection>
        <hr />
        <div className="subtitle is-4 is-spaced is-size-5-mobile">Thesis</div>
        <PublicationSelection projectFilterFunction={(pub) => {
            return pub.category == "thesis"
          }}></PublicationSelection>
        <hr />
      </div>
    </div>
  </Layout>
)

export default Publications
